import React, { Component } from "react";
import {
  BrowserRouter as Router, Route, Routes
} from "react-router-dom";
import "./App.css";
import { Home } from "./Components/pages/home/Home";
import { Statistics } from "./Components/pages/statistics/Statistics";
import { getTodayCovidData, getTodayTotalCovidData } from "./Service/covidNinja/NinjaService";

export default class App extends Component {
  constructor() {
    super();
    this.state = {
      dataset: {},
      totalInfo : {},
      isLoaded: false
    };
  }

  componentDidMount() {
    let dataset = {}
    getTodayCovidData().then(response =>{
      dataset = response.filter((d)=>{ return d.country !="Western Sahara"})
      getTodayTotalCovidData().then(response =>{
            this.setState({
            dataset,
            totalInfo: response,
            isLoaded: true
          });
      });
    });
   
  }
  
  render() {
    if (!this.state.isLoaded) {
      return "";
    }
    console.log("App render")
    console.log("v101")
    return (
      //<Router basename="/coronaMap">

      <Routes>
        <Route
          exact
          path={"/coronaMap"}
          element={
            <Home
              dataset={this.state.dataset}
              totalInfo={this.state.totalInfo}
            />
          }
        />
        <Route exact path={"/statistics"} element={<Statistics />} />
      </Routes>
    );
  }


}
